export default {
	// 从父组件传值的 共有的有主标题，链接更多，歌单数据
	props: {
		title: {
			type: String,
			default: ''
		},
		link: {
			type: String,
			default: ''
		},
		list: {
			type: Array,
			default() {
				return [];
			}
		},
		scrollX: {
			type: Boolean,
			default: false
		}
	},
		
	computed: {
		top() {
			return this.scrollX ? '0px' : '10px'
		}
	},
	data() {
		return {};
	},
	methods: {}
};
